.btn {
    font-family: $gilroy-bold;
	font-size: 16px;
    line-height: 36px;
    letter-spacing: -0.16px;
    text-transform: uppercase;
    border-radius: 0;
    transition: background-image .2s ease-in;

    &.btn-primary {
        background-image: linear-gradient(106deg, #fa1628, #b51421 107%);
        color: $white;

        &:hover {
            background-image: linear-gradient(4deg, #fa1628, #a30714 77%);
            color: $white;
        }
    }

    &.has-icon {
        padding: 10px 40px 10px 60px;

        span {
            margin-left: 20px;

            img {
                height: 11px;
            }
        }
    }
}