  position: absolute;
  top: 100%;
  right: 0;
  padding: 0;
  margin: 0;
  min-width: 100px;
  max-height: 0;
  overflow: hidden;
  background: #fff;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.16);
  border-radius: 0.25rem;
  transition: max-height 0.5s ease-out;
