header {
    position: relative;
    z-index: 2;

    .topbar {
        font-family: $gilroy-semibold;
        color: $body-color-light;
        font-size: 13px;
        line-height: 36px;
        letter-spacing: -0.13px;
        border-bottom: 1px dashed $border-color;

        .contact-information {
            display: flex;
            align-items: center;

            &-wrapper {
                display: flex;
            }

            &:not(:last-of-type) {
                margin-right: 60px;    
            }

            &--icon {
                display: flex;
                align-items: center;
                width: 16px;
                height: 16px;
                margin-right: 10px;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                    object-position: left;
                }
            }

            &--content {
                margin: 0;

                a {
                    color: $body-color-light;

                    &:hover {
                        color: $primary;
                    }
                }
            }
        }

        &-nav {
            margin: 0;

            li {
                display: inline-block;
                list-style-type: none;
                font-size: 14px;
                line-height: 36px;
                letter-spacing: -0.14px;
                font-family: $gilroy-bold;

                a {
                    display: inline-block;
                    color: $body-color-light;
                    padding: 7px 20px;
                    transition: background .2s ease-in;

                    &:hover {
                        background-color: #F5F5F5;
                        color: $body-color;
                    }

                    &.active,
                    &:active {
                        background-color: $primary;
                        color: $white;
                    }
                }

                @at-root {
                    .language-box {
                        position: relative;
                        display: inline-block;
                        padding: 7px 20px;
                        cursor: pointer;
                        z-index: 1;
                        transition: background .2s ease-in;

                        > span {
                            img {
                                margin-left: 5px;
                            }
                        }

                        &:hover {
                            background-color: #F5F5F5;

                            span {
                                img {
                                    transform: rotate(180deg);
                                }
                            }
                        }

                        .language-lists {
                            position: absolute;
                            top: 100%;
                            right: 0;
                            padding: 0;
                            margin: 0;
                            min-width: 100px;
                            max-height: 0;
                            overflow: hidden;
                            background: $white;
                            box-shadow: 0px 1px 3px rgba($black, 0.16);
                            border-radius: 0.25rem;
                            transition: max-height .5s ease-out;

                            li {
                                display: block;

                                a {
                                    display: block;
                                    padding: 5px 15px;

                                    &.active,
                                    &:hover {
                                        background-color: #f1f1f1;
                                    }
                                }
                            }
                        }

                        &:hover {
                            .language-lists {
                                max-height: 240px;
                                padding: 0;
                                margin: 0;
                                width: 100%;
                                display: block;
                            }
                        }
                    }
                }
            }
        }
    }

    .navbar {
        position: relative;
        padding: 0;

        &.navbar-light {
            .navbar-brand {
                img {
                    height: 75px;
                }
            }

            .navbar-nav {
                margin-left: 100px;

                .nav-item {
                    padding: 0 $nav-link-padding-x * 1.7;
                    line-height: 135px;

                    .nav-link {
                        position: relative;
                        color: $body-color-light;
                        font-family: $gilroy-bold;
                        font-size: 18px;
                        letter-spacing: -0.18px;
                        padding: 0;

                        &:hover,
                        &.active {
                            color: $primary;
                        }

                        &.active {
                            &.dropdown-toggle::after {
                                background-image: url('../img/icons/up-arrow-primary.svg');
                                transform: rotate(180deg);
                            }
                        }
                    }

                    &.dropdown {
                        position: initial;

                        .dropdown-toggle::after {
                            content: '';
                            position: absolute;
                            top: 3px;
                            right: -18px;
                            width: 10px;
                            height: 100%;
                            background: url('../img/icons/down-arrow.svg') no-repeat center;
                            background-size: 10px;
                            border: 0;
                        }

                        .dropdown-menu {
                            width: 100%;
                            background: $white;
                            left: 0;
                            top: 100%;
                            padding: 80px 0;
                            margin: 0;
                            border: 0;
                            border-bottom: 1px solid rgba($black, 0.08);
                            border-top: 1px dashed $border-color;
                            border-radius: 0;
                            transition: all 0.3s linear;

                            .submenu {
                                line-height: 1.2;

                                h3 {
                                    font-family: $gilroy-semibold;
                                    font-size: 20px;
                                    line-height: 28px;
                                    letter-spacing: -0.2px;
                                    margin-bottom: 30px;
                                }
                                .btn {
                                    text-transform: initial;
                                    font-family: $gilroy-semibold;
                                    padding: 15px 60px;
                                    line-height: 1.2;
                                }
                                ul {
                                    @extend .list-unstyled;

                                    li {
                                        line-height: 1.2;

                                        a {
                                            font-family: $gilroy-semibold;
                                            font-size: 16px;
                                            line-height: 36px;
                                            color: $body-color-light;

                                            &:hover,
                                            &.active {
                                                color: $primary;
                                            }
                                        }
                                    }
                                }
                                &-image {
                                    text-align: right;
                                    height: 350px;

                                    img {
                                        height: 100%;
                                        max-width: 100%;
                                        object-fit: contain;
                                        object-position: top;
                                    }
                                }
                            }
                        }

                        &:hover {
                            .nav-link {
                                color: $primary;
                            }
                            .dropdown-toggle::after {
                                background-image: url('../img/icons/up-arrow-primary.svg');
                            }
                        }

                        @include media-breakpoint-up(lg) {
                            &:hover {
                                .dropdown-menu {
                                    display: block;
                                }
                            }
                        }
                    }
                }
            }

            .navbar-toggler {
                &-icon {
                    position: relative;
                    width: 20px;
                    height: 20px;
                    background-image: none;

                    .menu-icon {
                        position: absolute;
                        right: 0;
                        display: block;
                        width: 100%;
                        height: 3px;
                        background-color: $body-color;
                        transition: all 0.3s ease-out;

                        &:nth-child(1) {
                            top: 0;
                        }

                        &:nth-child(2) {
                            top: 8px;
                            right: 50%;
                            width: 60%;
                            transform: translateX(50%);
                        }

                        &:nth-child(3) {
                            top: 16px;
                        }
                    }
                }

                &[aria-expanded='true'] {
                    .navbar-toggler-icon {
                        .menu-icon {
                            &:nth-child(1) {
                                transform: rotate(45deg);
                                top: 7px;
                            }
                            &:nth-child(2) {
                                opacity: 0;
                            }
                            &:nth-child(3) {
                                transform: rotate(-45deg);
                                top: 7px;
                            }
                        }
                    }
                }

                &:focus {
                    outline: 0;
                    box-shadow: none;
                }
            }
        }

        &.sticky {
            position: fixed;
            z-index: 1;
            padding: 15px 0;
            left: 0;
            right: 0;
            top: -96px;
            width: 100%;
            background-color: $white;
            transform: translateY(96px);
            transition: transform 1s, -webkit-transform 1s;
            box-shadow: 0 0 10px rgba($black, 0.1);
        }
    }

    .navbar-toggler {
        &:focus {
            outline: 0;
            box-shadow: none;
        }
    }
}

@media (max-width: 1320px) {
    header {
        .navbar {
            &.navbar-light {
                .navbar-nav {
                    margin-left: 30px;

                    .nav-item {
                        padding: 0 1.5rem;
                    }

                    .btn {
                        &.has-icon {
                            padding: 10px 20px 10px 20px;
                        }
                    }
                }
            }
        }
    }
}

// Below 1200px
@include media-breakpoint-down(xl) {
    header {
        .navbar {
            &.navbar-light {
                .navbar-brand {
                    img {
                        height: 50px;
                    }
                }

                .navbar-nav {
                    .nav-item {
                        padding: 0 1rem;

                        .nav-link {
                            font-size: 15px;
                        }

                        &.dropdown {
                            .dropdown-menu {
                                a {
                                    padding: 7px 10px;
                                    font-size: 15px;
                                }
                            }
                        }
                    }
                }

                .btn {
                    font-size: 15px;
                }
            }
        }
    }
}

// Below 992px
@include media-breakpoint-down(lg) {
    header {
        .navbar {
            padding: 20px 0;

            &.navbar-light {
                .navbar-nav {
                    margin: 0;
                    text-align: right;

                    .nav-item {
                        line-height: 1.4;
                        padding: 15px 0;

                        .nav-link {
                            font-size: 24px;
                        }

                        &.dropdown {
                            .dropdown-toggle {
                                padding-right: 20px;

                                &:after {
                                    right: 0;
                                }
                            }

                            .dropdown-menu {
                                padding: 0;
                                border: 0;

                                .submenu {
                                    padding-top: 20px;

                                    >.container {
                                        max-width: 100%;
                                        padding: 0;
                                    }

                                    &-info,
                                    &-image {
                                        display: none;
                                    }

                                    &-nav {
                                        text-align: right;
                                    }
                                }
                            }
                        }
                    }
                }
                .navbar-collapse {
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 101px;                    
                    background: $white;
                    padding: 50px 0;

                    &.show {
                        display: flex;
                        align-items: flex-end;
                        justify-content: space-between;
                        flex-direction: column;
                        height: calc(100vh - 100px);
                        overflow-y: auto;
                    }

                    >* {
                        width: 100%;
                    }

                    >ul {
                        max-width: 720px;
                        margin: 0 auto;
                        padding: 0 24px;

                        .btn {
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
}

// Below 768px
@include media-breakpoint-down(md) {
    header {
        .navbar {
            &.navbar-light {
                .navbar-collapse {
                    >ul {
                        max-width: 540px;
                    }
                }
            }
        }
    }
}