footer {
	.company-info-wrapper {
		padding: 35px 0;
		border-top: 1px dashed $border-color;
		border-bottom: 1px dashed $border-color;

		.footer-logo {
			img {
				height: 75px;
			}
		}
	}

	.contact-detail {
		&-grid {
			display: grid;
			grid-template-columns: repeat(3, max-content);
			column-gap: 90px;
		}
		&-col {
			font-size: 14px;
			line-height: 24px;
			letter-spacing: -0.14px;

			h6 {
				font-family: $gilroy-semibold;
				font-size: 14px;
				margin: 0;
			}
			p,
			a {
				font-family: $gilroy-medium;
				margin: 0;
				color: $body-color-light;
			}
		}
	}

	.footer-nav {
		padding: 65px 0 35px;

		.list-company-info {
			@extend .list-unstyled;
			margin: 40px 0 30px;

			li {
				position: relative;
				padding-left: 20px;
				font-size: 12px;
				line-height: 24px;
				font-family: $gilroy-medium;

				&:before {
					content: '';
					position: absolute;
					left: 0;
					top: 8px;
					width: 10px;
					height: 10px;
					background: url('../../assets/img/icons/checked-primary.svg') center left no-repeat;
					background-size: 10px;
				}

				a {
					color: $body-color-light;

					&:hover {
						color: $primary;
					}

					span {
						svg {
							width: 12px;
							height: 15px;
							vertical-align: sub;
							fill: currentColor;
						}
					}
				}
			}
		}

		h5 {
			font-size: 16px;
			line-height: 21px;
  			letter-spacing: -0.10px;
			font-family: $gilroy-semibold;
		}

		.quick-links {
			&-grid {
				display: grid;
				grid-template-columns: repeat(4, max-content);
				column-gap: 100px;
			}
			&-col {
				h6 {
					margin-bottom: 0;
				}
				h6,
				a,
				.title-link a {
					font-size: 14px;
					line-height: 36px;
					letter-spacing: -0.14px;
					font-family: $gilroy-semibold;
					color: $body-color;
				}
				ul {
					@extend .list-unstyled;
					margin-bottom: 30px;

					li {
						a {
							color: $body-color-light;
							font-family: $gilroy-medium;
							line-height: 30px;

							&:hover,
							&.active {
								color: $primary;
							}
						}
					}
				}
			}
		}
	}

	.copyright {
		padding: 45px 0;
		border-top: 1px dashed $border-color;

		&-link {
			a {
				position: relative;
				display: inline-block;
				color: $body-color-light;

				&:hover {
					// opacity: .7;
					color: $primary;
				}
			}
		}

		&-row {
			display: flex;
			justify-content: space-between;

			p,
			a {
				font-family: $gilroy-medium;
				font-size: 12px;
				line-height: 16px;
				color: $body-color-light;
			}

			p {
				margin: 0;

				&:not(:first-of-type) {
					margin-top: 5px;
				}

				.company-name {
					display: block;
				}
			}

			.badge-logos {
				display: grid;
				align-items: center;
				grid-template-columns: repeat(4, 1fr);
				column-gap: 10px;

				>div {
					text-align: center;

					img {
						max-height: 70px;
						width: auto;
					}
				}
			}

			.list-policy {
				@extend .list-unstyled;
				@extend .mb-0;

				li {
					position: relative;
					@extend .list-inline-item;

					&:not(:first-of-type) {
						&::before {
							content: '|';
							position: absolute;
							left: -7px;
							top: 4px;
							font-size: 12px;
						}
					}

					a {
						&:hover {
							color: $primary;
						}
					}
				}
			}

			.language-box {
				padding: 0;
				font-family: $gilroy-medium;
				font-size: 12px;
				line-height: 16px;
				color: $body-color-light;

				&:hover {
					background-color: transparent;

					>span {
						color: $body-color;
					}
				}

				.language-lists {
					top: auto;
					bottom: 100%;
					text-align: center;

					li {
						a {
							padding: 10px 15px;
						}
					}
				}
			}
		}
	}
}

// Below 1200px
@include media-breakpoint-down(xl) {
	footer {
		.company-info-wrapper {
			.contact-detail-grid {
				column-gap: 40px;
			}
		}
		.footer-nav {
			.quick-links-grid {
				column-gap: 40px;
			}
		}
	}
}

// Below 992px
@include media-breakpoint-down(lg) {
	footer {
		.company-info-wrapper {	
			.footer-logo {
				img {
					height: 40px;
				}
			}
		}
		.footer-nav {
			padding: 30px 0 0;

			.list-company-info {
				margin: 0;

				li {
					padding: 3px 0 3px 20px;
					font-size: 15px;

					a {
						span {
							svg {
								width: 16px;
								height: 19px;
							}
						}
					}

					&:before {
						top: 11px;
					}
				}
			}

			h5 {
				font-size: 18px;
				line-height: 25px;
				margin-bottom: 20px;
			}

			.quick-links {
				&-grid {
					justify-content: space-between;
				}
				&-col {
					h6 {
						font-size: 12px;
						line-height: 24px;
					}
					a,
					.title-link a {
						font-size: 12px;
						line-height: 14px;
					}
					ul {
						li {
							a {
								line-height: 14px;
							}
						}
					}
				}
			}
		}
		.contact-detail {
			&-grid {
				padding-bottom: 30px;
				margin-bottom: 30px;
				border-bottom: 1px dashed $border-color;
			}
		}
		.border-bottom-after {
			position: relative;
			padding-bottom: 35px;
			margin-bottom: 35px;

			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				height: 1px;
				width: 100%;
				border-bottom: 1px dashed $border-color;
			}
		}
		.copyright {
			padding-top: 0;
			border-top: 0;

			&-row {
				p {
					.company-name {
						display: inline;
					}
				}
				.badge-logos {
					margin-top: 20px;
				}
			}
		}
	}
}

// Below 768px
@include media-breakpoint-down(md) {
	footer {
		.contact-detail {
			&-grid {
				display: block;
			}
			&-col {
				&:not(:last-of-type) {
					margin-bottom: 20px;
				}
			}
		}
		.footer-nav {
			.list-company-info {
				margin-bottom: 30px;
			}
		}
	}
}

// Below 576px
@include media-breakpoint-down(sm) {
	footer {
		.footer-nav {
			.quick-links {
				&-grid {
					grid-template-columns: repeat(2, calc(50% - 50px));
				}
			}
		}
		.copyright-row {
			.badge-logos {
				column-gap: 5px;

				> div {
					text-align: left;
					img {
						max-height: 45px;
					}
				}
			}
		}
	}
}